<template>
	<div id="deliveryer-deliveryer">
		<public-header title="配送员列表"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.work_status == 1}" @click="onChangeWorkStatus(1)">工作中</div>
					<div class="tab-item" :class="{active: filter.items.work_status == 0}" @click="onChangeWorkStatus(0)">休息中</div>
				</div>
				<van-search placeholder="请输入配送员名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="deliveryer-list"
				>
					<deliveryer-item
						:deliveryer="item"
						:vue="vue"
						:index="index"
						v-for="(item, index) in records.data"
						:key="item.id"
					></deliveryer-item>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import deliveryerItem from "./components/deliveryer"
	import {getRecords} from "../../controller/funcCommon";
	export default {
		data() {
			return {
				records: {
					page: 1,
					psize: 20,
					loading: false,
					finished: false,
					empty: false,
					data: []
				},
				showPreLoading: true,
				vue: this,
				isRefresh: false,
				filter: {
					items: {
						work_status: 1
					}
				}
			}
		},
		components: {
			publicHeader,
			deliveryerItem
		},
		methods: {
			onLoad(force) {
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/deliveryer/plateform/list'
				});
			},
			onChangeWorkStatus: function(value) {
				value = parseInt(value);
				if(this.filter.items.work_status == value) {
					return;
				}
				this.filter.items.work_status = value;
			},
			onPullDownRefresh() {
				this.onLoad(true);
			}
		},
		mounted() {
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
</style>


