<template>
	<div id="deliveryer-item">
		<div class="deliveryer-item margin-10">
			<router-link tag="div" :to="util.getUrl({path: '/pages/deliveryer/guide', query: {id: deliveryer.id}})">
				<van-row>
					<van-col span="5">
						<div class="deliveryer-img">
							<img class="img-100" :src="deliveryer.avatar" alt="">
						</div>
					</van-col>
					<van-col span="19">
						<div class="deliveryer-title flex-lr">
							<div class="font-bold"><span class="number font-bold">#{{deliveryer.id}}</span>{{deliveryer.title}}</div>
							<div class="itag itag-danger" v-if="deliveryer.work_status == 1">工作中</div>
							<div class="itag itag-disabled" v-if="deliveryer.work_status == 0">休息中</div>
						</div>
						<div class="flex">
							<div class="font-15 c-gray" >账户余额：<span class="font-15 c-danger">￥{{deliveryer.credit2}}</span></div>
						</div>
						<div class="phone flex-lr">
							<div class="font-15 c-gray" @click.stop="util.jsUrl('tel:' + deliveryer.mobile)">电话：<span class="c-info">{{deliveryer.mobile}}</span></div>
							<div class="font-15 c-gray">
								<span :class="{'c-info': deliveryer.is_takeout == 1}">外卖</span>
								<span class="c-default">/</span>
								<span :class="{'c-info': deliveryer.is_errander == 1}">跑腿</span>
							</div>
						</div>
					</van-col>
				</van-row>
			</router-link>
			<div class="operate flex-lr border-1px-t">
				<iswitch :value="deliveryer.work_status" text-open="工作" text-close="休息" @change="jsToggleSwitch" :extra="{key: 'deliveryer.work_status', name: 'work_status', id: deliveryer.id}"></iswitch>
				<iswitch :value="deliveryer.extra.accept_wechat_notice" text-open="模板" @change="jsToggleSwitch" :extra="{key: 'deliveryer.extra.accept_wechat_notice', name: 'accept_wechat_notice', id: deliveryer.id}"></iswitch>
				<iswitch :value="deliveryer.extra.accept_voice_notice" text-open="语音" @change="jsToggleSwitch" :extra="{key: 'deliveryer.extra.accept_voice_notice', name: 'accept_voice_notice', id: deliveryer.id}"></iswitch>
			</div>
		</div>
	</div>
</template>

<script>
	import iswitch from "@/components/switch"
	export default {
		components: {
			iswitch
		},
		props: {
			deliveryer: Object,
			vue: Object,
			index: 0,
		},
		data() {
			return {
			}
		},
		methods: {
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					url: 'plateform/deliveryer/plateform/change_status',
					key: res.key,
					data: {
						id: res.id,
						type: res.name,
						value: res.value,
					}
				});
			},
		},
		mounted() {
		}
	}
</script>

<style>
	#deliveryer-item .deliveryer-item{
		background-color: #fff;
		border-radius: 3px;
	}
	#deliveryer-item .deliveryer-item .deliveryer-img{
		width: 55px;
		height: 55px;
		padding-top: 10px;
		padding-left: 10px;
	}
	#deliveryer-item .deliveryer-item .deliveryer-img img{
		border-radius: 100%;
	}
	#deliveryer-item .deliveryer-item .deliveryer-title{
		padding-top: 10px;
		padding-right: 10px;
		height: 55px;
	}
	#deliveryer-item .deliveryer-item .deliveryer-title .number{
		padding-right: 10px;
	}
	#deliveryer-item .deliveryer-item .phone{
		padding: 5px 10px 10px 0;
	}
	#deliveryer-item .deliveryer-item .operate{
		padding: 15px 10px 10px;
	}
</style>


